<template>
    <div class="notes-page">
        <div class="tabs-note">
            <span @click="tabsTogg(0)" :class="{'acti': tabs==0}">全部</span>
            <span @click="tabsTogg(1)" :class="{'acti': tabs==1}">未结算</span>
            <span @click="tabsTogg(2)" :class="{'acti': tabs==2}">中奖</span>
        </div>

        <div class="box">
            <div class="mui-scroll">
                <div class="box1" v-if="getSscBetsList">
                    <ul v-if="getSscBetsList.sscBetsList.length>0">
                        <li v-for="item in getSscBetsList.sscBetsList" :key="item.pk">
                            <div class="one">
                                <div class="fl">
                                    <span>{{item.playGroupName}}-{{item.content}}</span>
                                    <var v-if="item.status == 0" class="v1">未结算</var>
                                    <var v-else-if="item.status == 1 && item.winOrLose > 0" class="v2">已中奖</var>
                                    <var v-else-if="item.status == 1 && item.winOrLose < 0" class="v3">未中奖</var>
                                    <var v-else-if="item.status == 2" class="v4">已撤单</var>
                                </div>
                                <div class="fr">
                                    <i>{{item.winOrLose}}</i>
                                    <span>元</span>
                                </div>
                            </div>
                            <div class="two">
                                <span class="fl">期号：{{item.number}}</span>
                                <span class="fr">{{item.createTime | Date}}</span>
                            </div>
                        </li>
                    </ul>
                    <div v-else class="null-data">没有任何记录</div>
                </div>
                <div class="box1 c" v-else>
                    <div class="null-data">
                        <loadding></loadding>
                    </div>
                </div>
            </div>
        </div>
        

        <div class="btn">
            <button @click="$emit('close')">关闭</button>
        </div>
    </div>
</template>

<script>
    import { mapState } from "vuex";
    import { RECORD_GET_SSC_BETS_LIST } from "@/tool/common/constants.js";
    import { thisMonth } from "@/tool/common/time.js";
    
    export default {

        data(){
            return {
                tabs: 0,
            }
        },
        created(){
            this.getNetData()
        },
        computed: {
            ...mapState('Record', {
                getSscBetsList: state => state[RECORD_GET_SSC_BETS_LIST]
            })
        },
        mounted(){
            // this.$nextTick(()=>{
            //     this.mui(".notes-page .box").scroll({
            //         deceleration:0.0005
            //     })
            // })
        },
        methods: {
            tabsTogg(tabs){
                this.tabs = tabs
                this.mui('.notes-page .box').scroll().scrollTo(0,0,100);//100毫秒滚动到顶
                this.getNetData()
            },
            getNetData(){
                this.$store.commit(`Record/SET_DATA`, false)
                var month = thisMonth()
                var data = {
                    ...this.baseUser,
                    pageIndex:1,
                    pageSize:100,
                    startTime: month.thisMonthStart,
                    endTime: month.thisMonthEnd,
                    playGroupId: '',
                    status:'',
                    isZhongjiang:''
                }
                switch (this.tabs) {
                    case 1:
                        data.status = 0
                        break;
                    case 2:
                        data.status = 1
                        data.isZhongjiang = true
                        break;
                    default:
                        break;
                }
                this.$store.dispatch(`Record/${RECORD_GET_SSC_BETS_LIST}`, data)
                .then(()=>{
                    setTimeout(() => {
                        this.mui(".notes-page .box").scroll({
                            deceleration:0.0005
                        })
                    }, 500);
                })
            }
        },
    }
</script>

