<template>
    <div class="explain">
        <div class="layout">
            <h2>玩法说明 <span @click="close" class="close ico-close-white"></span></h2>
            <div class="content">
                <div class="mui-scroll">
                    <li v-for="item in data" :key="item.b">
                        <b>{{item.text.b}}</b>
                        <p v-for="item1 in item.text.p" :key="item1">{{item1}}</p>
                    </li>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['data'],
        data(){
            return {

            }
        },
        methods: {
            close(){
                this.$emit('close', false);
            }
        },
        mounted(){
            this.mui(".content").scroll({
                scrollY: true, //是否竖向滚动
                scrollX: false, //是否横向滚动
                startX: 0, //初始化时滚动至x
                startY: 0, //初始化时滚动至y
                indicators: true, //是否显示滚动条
                deceleration: 0.0006, //阻尼系数,系数越小滑动越灵敏
                bounce: true //是否启用回弹
            })
        }
    }
</script>

<style lang="stylus">
    .explain
        position absolute
        left 0
        right 0
        bottom 0
        top 0
        display flex
        align-items center
        justify-content center
        .layout
            width 94%
            height 4rem
            background rgba(#000, .8)
            display flex
            flex-direction column
            h2
                text-align center
                border-bottom .01rem solid #ccc
                line-height .8rem
                font-size 0.32rem
                flex-shrink 0
                position relative
                color #fff
                .close
                    position absolute
                    right 0
                    top 0
                    width .8rem
                    height .8rem
                    z-index 1
            .content
                flex-grow 1
                overflow hidden
                margin .2rem 0
                position relative

                p
                    line-height 1.6
                    color #fff
                li
                    float left
                    width 100%
                    margin-top .3rem
                    padding 0 .3rem
                    &:first-child
                        margin-top 0
                    b
                        color #fff
                        display block
                        padding-bottom .2rem
                    p
                        color #fff
</style>