<template>
    <div class="container">
        <div class="fixed">
            <div class="shishicai-page">
                <div class="title flex-shrink">
                    <div v-if="openTime2">
                        <h2>{{detail.name || default_data['d' + playGroupId].name }}<span @click="show5=!show5">玩法说明</span> </h2>
                        <div class="fr" v-if="show3">
                            <h3>封盘中···</h3>
                        </div>
                        <div class="fr" v-else>
                            <span>距</span>
                            <span class="color m">{{nextNumber}}期</span>
                            <span class="m">截止:</span>
                            <span class="color m">{{timeSecound}}</span>
                        </div>
                    </div>
                    <div v-else class="loadding1">加载中...</div>
                </div>

                <!-- 开奖区域 -->
                <div :class="['number flex-shrink',{'active': show}]">
                    <div v-if="sscHistoryList" class="number-if">
                        <ul>
                            <li v-for="(item, index) in sscHistoryList.sscHistoryList" :key="index">
                                <b>第{{item.number | threeNumber}}期</b>
                                <div class="number-box" v-if="item.openCode !== ''">
                                    <!-- 开奖号码 动态组件 -->
                                    <component v-bind:is="currentTabComponent" :number="item.openCode=== false ? default_data['d' + playGroupId].history :item.openCode.split(',')"></component>
                                    <!-- <template v-for="(item1, index1) in "> -->
                                        <!-- <span :class="'c0' + item1" :key="index1">{{item1}}</span> -->
                                    <!-- </template> -->
                                </div>
                                <div class="kai" v-else>开奖中</div>
                            </li>
                        </ul>
                        <div @click="show=!show" class="down"></div>
                    </div>
                    <div v-else class="loadding1">加载中...</div>
                </div>

                <!-- 动态子组件 -->
                <keep-alive>
                    <component class="flex-grow" v-bind:is="'play'.concat(playGroupId)"></component>
                </keep-alive>

                <!-- 底部结构 -->
                <div class="abs-touzhu flex-shrink">
                    <div class="fl">
                        <div class="note" @click="show4=true">记录</div>
                        <div class="money">{{UserBaseInfo.balance}}</div>
                        <div class="chongzhi" v-if="false"><router-link to="/Recharge">充值></router-link></div>
                    </div>
                    <div class="fr">
                        <div class="num" @click="show2=true; out=false;">
                            <span :class="'sp' + chip">￥</span>
                            <h3>{{zhu_code[chip]}}</h3>
                        </div>
                        <div class="button" @click="Betting">立即竞猜</div>
                    </div>
                </div>
            </div>

            <!-- 确认投注弹窗 -->
            <div class="alert-note" v-if="show1">
                <div class="layout">
                    <h2>{{detail.name}}-第{{openTime2.number}}期</h2>
                    <ul>
                        <li v-for="(item,index) in sscBetListData" :key="index">{{item.content}} {{item.zhushu}}注 {{item.perMoney}}元</li>
                    </ul>
                    <div class="btn-group">
                        <span class="cancel" @click="show1=false">取消</span>
                        <span class="confirm" @click="betForm">确认</span>
                    </div>
                </div>
            </div>

            <!-- 注码 -->
            <div :class="['alert-zhu',{'out':out}]" v-if="show2">
                <h2>设置筹码 <span class="close" @click="close(false)"></span></h2>
                <ul>
                    <li :class="{'acti': zhu_index==0}">
                        <div @click="zhu_index=0" class="box">
                            <i class="ico-chou1">{{zhu_code[0]}}</i>
                            <span>当前筹码</span>
                        </div>
                    </li>
                    <li :class="{'acti': zhu_index==1}">
                        <div @click="zhu_index=1" class="box">
                            <i class="ico-chou2">{{zhu_code[1]}}</i>
                            <span>当前筹码</span>
                        </div>
                    </li>
                    <li :class="{'acti': zhu_index==2}">
                        <div @click="zhu_index=2" class="box">
                            <i class="ico-chou3">{{zhu_code[2]}}</i>
                            <span>当前筹码</span>
                        </div>
                    </li>
                    <li :class="{'acti': zhu_index==3}">
                        <div @click="zhu_index=3" class="box">
                            <i class="ico-chou4">{{zhu_code[3]}}</i>
                            <span>当前筹码</span>
                        </div>
                    </li>
                    <li :class="{'acti': zhu_index==4}">
                        <div @click="zhu_index=4" class="box">
                            <i class="ico-chou5">{{zhu_code[4]}}</i>
                            <span>当前筹码</span>
                        </div>
                    </li>
                    <li :class="{'acti': zhu_index==5}">
                        <div @click="zhu_index=5" class="box">
                            <b-input class="ico-chou6" :minNumber="100" v-model="zhu_code[5]" :maxLength="5" placeholder="自定义"></b-input>
                            <span>[修改筹码]</span>
                        </div>
                    </li>
                </ul>
                <div class="btn">
                    <div class="button" @click="close(true)">确定</div>
                </div>
            </div>

            <!-- 封盘组件 -->
            <!-- <sealed-disk v-if="show3"></sealed-disk> -->

            <!-- 投注记录 -->
            <Record @close="show4=false" v-if="show4"></Record>

            <!-- 玩法说明 -->
            <Explain v-if="show5" @close="show5=false" :data="exdata"></Explain>

        </div>
    </div>
</template>

<script>
    
    import $ from "jquery";

    import { mapState } from "vuex";

    // import desc_shuoming from "./wf.js";
    import desc_shuoming from "./smwf.js";
    
    import {
        GAME_GET_SSC_PLAY_GROUP,
        GAME_GET_PLAN_OPEN_DATA_HISTORY,
        GAME_GET_SSC_OPEN_TIME2,
        GAME_GET_BET2,
        GAME_GET_USER_BASE_INFO
    } from "@/tool/common/constants.js";

    import Record from "./components/record";
    import Explain from "./components/Explain";
    
    import default_data from "./default.js";

    export default {
        components: {
            Record,
            Explain,
        },
        data(){
            return {

                currentTabComponent:'',

                show: false,
                show1: false,
                show2: false,
                show3: false,
                show4: false,

                show5: false,

                timeSecound: '00:00',
                chip:0,

                playGroupId:'',

                zhu_index: 0,
                zhu_code: [1, 2, 5, 10, 50, 100],

                out:false,

                time: 0,
                down_time:0,
                exdata: {},


                leftTime:0,

                default_data: default_data
            }
        },
        created(){

            // 彩种ID
            this.playGroupId = this.$route.params.type;

            // 开奖动态组件
            this.currentTabComponent = 'OpenCode-'.concat(this.playGroupId);

            this.$store.commit('Game/SET_PLAY_GROUP_ID', this.$route.params.type);

            this.$store.commit('User/SET_USER', this.$user());
   
            // 彩种详情
            this.getSscPlayGroup();

            // 开奖时间
            this.getSscOpenTime2();

            // 个人信息
            this.getUserBaseInfo();


            // 金额缓存
            var local = this.$assist.getLocalStorage('CHIP');
            
            if(local){
                this.zhu_index = local.index;
                this.chip = local.index;
                this.zhu_code[local.index] = local.money;
                this.type_code = local.money + '';
                this.$store.commit('Game/SET_CHIP', local.money);
            }else{
                this.$store.commit('Game/SET_CHIP', this.zhu_code[0]);
            }

            // 游戏说明
            this.exdata = desc_shuoming['play' + this.playGroupId];
        },
        mounted(){
            this.time = setInterval(() => {
                this.getSscOpenTime2();
            }, 3000);
            document.body.addEventListener('focusout',()=>{     
                window.scrollTo({top:0,left:0,behavior:"smooth"});
            })
        },
        computed: {
            ...mapState('Game', {
                detail: state => state[GAME_GET_SSC_PLAY_GROUP],
                openTime2: state => state[GAME_GET_SSC_OPEN_TIME2],
                UserBaseInfo: state => state[GAME_GET_USER_BASE_INFO],
                sscBetListData: state => state.sscBetList,
                sscHistoryList: state => state[GAME_GET_PLAN_OPEN_DATA_HISTORY],  // 历史开奖
            }),
            nextNumber(){
                var next = this.openTime2.number
                var number = next.substring(next.length - 4, next.length)
                return number
            }
        },
        methods: {
            // 判断是否选注
            Betting(){
                if(this.show3){
                    this.$toast('封盘中···');
                    return false;
                }
                if (this.sscBetListData.length > 0){
                    this.show1 = true;
                }else{
                    this.$toast('请选择投注');
                }
            },
            // 关闭的时候 处理 -保存
            close(save){            
                if(save){
                    // if(this.zhu_index==5  && this.type_code==''){
                    //     this.$toast('请设置筹码')
                    //     return;
                    // }else if(this.zhu_index==5 && this.type_code == 0){
                    //     this.$toast('请设置筹码大于0')
                    //     return;
                    // }
                    this.out=true
                    setTimeout(() => {
                        this.show2=false
                    }, 600);
                    this.chip = this.zhu_index
                    this.$store.commit('Game/SET_CHIP', this.zhu_code[this.zhu_index])
                    this.$assist.setLocalStorage('CHIP', {index: this.zhu_index, money: this.zhu_code[this.zhu_index]})
                }else{
                    this.out=true
                    setTimeout(() => {
                        this.show2=false
                    }, 600);
                }
            },

            // 投注
            betForm(){
                this.$toast.loading({
                    duration: 0,
                    forbidClick: true,
                    message: '投注中...',
                })

                const sscBetList = this.sscBetListData

                var totalMoney = 0;

                // 统计总金额
                sscBetList.forEach(item=>{
                    totalMoney += parseFloat(item.totalMoney)
                })
                
                var betForm = {
                    totalZhushu: sscBetList.length,
                    totalMoney,
                    lianxuBeishu: '1',
                    lianxuQishu:'1',
                    sscBetList,
                }

                var data = {
                    betForm: JSON.stringify(betForm),
                    ...this.baseUser
                }

                console.log(data, totalMoney);
                
                this.$store.dispatch(`Game/${GAME_GET_BET2}`, data)
                .then(res=>{
                    this.$toast.clear()
                    if(res.result==1){
                        // H5具体调用方法如下：
                        this.sucCall(betForm, totalMoney)
                        this.show1=false
                        this.clearSelect()
                        this.getUserBaseInfo()
                        this.$toast(res.description);
                    }else{
                        this.$toast(res.description)
                    }
                })
            },
            encode(str){
                // 对字符串进行编码
                var encode = encodeURI(str);
                // 对编码的字符串转化base64
                var base64 = btoa(encode);
                return base64;
            },
            sucCall(betForm, totalMoney) {
                var betFormBase64 = this.encode(JSON.stringify(betForm))
                console.log(betFormBase64);
                
                var json = `{"goodsName":"${this.detail.name}投注了${totalMoney}元","goodsUrl":"/follow?data=${betFormBase64}","outhParam":"totalMoney=${totalMoney}",
                "goodsId":"${this.playGroupId}","activeId":"${this.openTime2.number}"}`;

                if(this.env.app){
                    if(this.env.isAndroid){
                        window.android.showMsgButton(json);
                    }
                    if(this.env.isIOS){
                        window.webkit.messageHandlers.showMsgButton.postMessage(json);
                    }
                }else{
                    console.log('非APP');
                }
            },
            // 清除选项
            clearSelect(){
                $('.fr-layout li').removeClass('active')
                this.$store.commit('Game/CLEAR_BET_LIST')
            },

            // 个人信息
            getUserBaseInfo(){
                this.$store.dispatch(`Game/${GAME_GET_USER_BASE_INFO}`, this.baseUser)
            },

            // 彩种详情
            getSscPlayGroup(){
                let data = {
                    playGroupId: this.playGroupId,
                }
                this.$store.dispatch(`Game/${GAME_GET_SSC_PLAY_GROUP}`, data)
            },

            // 历史开奖
            getPlanOpenDataHistory(){
                var data = {
                    size: 5,
                    playGroupId: this.playGroupId,
                }
                this.$store.dispatch(`Game/${GAME_GET_PLAN_OPEN_DATA_HISTORY}`, data)
            },

            // 倒计时
            cutdown(){
                if(this.down_time == 0){
                    this.jianjian();
                    this.down_time = setInterval(this.jianjian, 1000);
                }
            },

            jianjian(){
                let num = this.leftTime;
                if(num < 0){
                    clearInterval(this.down_time);
                    this.down_time = 0;
                    this.show3 = true;
                }else{
                    var minute = parseInt(num / 60)
                    minute = minute < 10 ? '0' + minute : minute
                    var second = num % 60
                    second = second < 10 ? '0' + second : second
                    this.timeSecound = minute + ':' + second
                }
                this.leftTime = this.leftTime - 1;
            },

            // 开奖时间
            getSscOpenTime2(){
                var data = {
                    playGroupId: this.playGroupId,
                }
                this.$store.dispatch(`Game/${GAME_GET_SSC_OPEN_TIME2}`, data)
                .then(res=>{
                    this.getPlanOpenDataHistory();
                    this.$store.commit('Game/UPDATE_NUMBER');
                    if(res.leftTime > 0){
                        this.leftTime = res.leftTime
                        this.show3 = false;
                        this.cutdown();
                    }else{
                        console.log('正在封盘');
                        this.show3 = true;
                    }
                })
            },
        },
        destroyed(){
            console.log('destroyed');
            clearInterval(this.time);
            clearInterval(this.down_time);
        },
        filters: {
            threeNumber(val){
                return val.substring(val.length - 4,)
            }
        }
    }
</script>

<style>
    
</style>
